.app {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 64px;
}

button {
    background-color: rgba(0,0,0,0);
    border: none;
    font-family: IBM Plex Sans ;
    font-size: 1.5rem;
    font-style: normal;
    font-weight: 400;
    margin-bottom: 12px;
}

button:hover {
    color: red;
    font-weight: 600;
    cursor: pointer;
}

.emphasis {
    font-weight: 700;
}

.intro-text {
    display: inline-flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 20px;
    max-width: 700px;
}

.intro-text h1 {
    color: #002649;
    font-family: IBM Plex Sans;
    font-size: 4rem;
    font-style: normal;
    font-weight: 500;
    margin: 0px;
}

.intro-text p {
    color: #002649;
    font-family: Hanuman;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 130%; /* 36.4px */
    margin: 0px;
}

.project-cards {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 40px;
    justify-content: space-between;
}

@media screen and (max-width: 512px) {
    .project-cards {
        width: 80vw;
    }
    .project-card {
        /* width: 80vw; */
    }
  }

  @media screen and (min-width: 513px) and (max-width: 1024px) {
    .project-cards {
        width: calc(80vw + 70px);
    }
    .project-card {
        width: calc(40vw - 60px);
    }
  }


  @media screen and (min-width: 1025px) {
    .project-cards {
        width: calc(60vw + 70px);
    }
    .project-card {
        width: calc(30vw - 60px);
    }
  }

.project-card {
    height: 450px;
    display: flex;
    flex-direction: column;
    padding: 30px 30px;
    align-items: center;
    transition: all 0.3s cubic-bezier(0.17, 1.03, 0.55, 1.02) 0s;
    justify-content: space-between;
}

.project-card-image-wrapper {
    display: flex;
    flex-direction: column;
    height: inherit;
    justify-content: center;
}

.project-card-image {
    object-fit: contain;
    max-width:100%;
    max-height:350px;
    /* margin: 24px; */
}

a {
    text-decoration: none;
}

.project-card-text {
    gap: 12px;
}

.project-card-text > a {
    text-decoration: none;
    text-decoration-style: none;
    text-align: left;
}

.project-card-text > p {
    color: #000;
font-family: IBM Plex Sans;
font-size: 16px;
font-style: normal;
font-weight: 300;
line-height: normal;
text-align: left;
}


.project-card-text h3 {
    color: #000;
font-family: IBM Plex Sans;
font-size: 20px;
font-style: normal;
font-weight: 700;
line-height: normal;
text-align: left;
}